
import Search from 'vux/src/components/search/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
components: {
XHeader, Group, Cell, Search, XButton, Popup, Grid, GridItem, Loading
},
data() {
return {
top_title: '企业管理员',
admins: [],
show: {addAdmin: false},
search: { auto_fixed: false, data: '', result: [] },
loadings:true
}
},
methods: {
getAdmins() {
axios.post('api/enterprise_admin/getAll', {enterprise_id: 1}).then(response => {
this.admins = response.data.data
this.loadings = false
}).catch(error => {
this.loadings = false
})
},
searchUser(mobile) {
this.loadings = true
axios.post('api/worker/getByMobile', {enterprise_id: 1, mobile: mobile}).then(response => {
this.search.result = response.data.data
this.loadings = false
}).catch(error => {
this.loadings = false
})
},
clearSearch() {
this.search.data = '';
},
addAdmin(user_id) {
axios.post('api/enterprise_admin/add', {enterprise_id: 1, user_id: user_id}).then(response => {
this.$vux.alert.show({
content: response.data.message
})
this.getAdmins()
}).catch(error => {
this.$vux.alert.show({
content: error.response.data.message
})
})
},
delAdmin(user_id) {
let _this = this
this.$vux.confirm.show({
title: '确定要删除管理员',
onConfirm() {
axios.post('api/enterprise_admin/del', {user_id: user_id, enterprise_id: 1}).then(response => {
_this.$vux.alert.show({
content: response.data.message
})
_this.getAdmins()
}).catch(error => {
_this.$vux.alert.show({
content: error.response.data.message
})
})
}
})
}
},
mounted() {
this.getAdmins()
}
}



